.content {
    background-color: #111;
    width: 100%;
}

::-webkit-scrollbar {
    /* tùy chỉnh thanh scrollbar */
    width: 100%;
    height: 0.5rem;
}

::-webkit-scrollbar-thumb {
    background-color: #222;
    height: 0.4rem;
    opacity: 0.8;
}

::-webkit-scrollbar-track {
    background-color: #444;
}